.sky-map-container,.event-calendar-container,.mars-rover-container,.space-news-container{max-width:1000px;margin:0 auto;padding:2rem 1rem}.sky-map-container h1,.event-calendar-container h1,.mars-rover-container h1,.space-news-container h1{font-size:2.5rem;color:gold;margin-bottom:1rem;text-align:center}.sky-map-container>p,.event-calendar-container>p,.mars-rover-container>p,.space-news-container>p{text-align:center;font-size:1.2rem;margin-bottom:2.5rem;color:#e0e0e0}.placeholder-content{background:rgba(17,25,40,.75);border-radius:12px;backdrop-filter:blur(8px);-webkit-backdrop-filter:blur(8px);border:1px solid hsla(0,0%,100%,.125);padding:1.5rem;box-shadow:0 8px 32px 0 rgba(0,0,0,.36);display:flex;flex-direction:column;align-items:center}.placeholder-card{max-width:600px;width:100%}.placeholder-card h2{color:gold;margin-bottom:1.5rem;font-size:1.5rem}.placeholder-card p{margin-bottom:1rem}.placeholder-card ul{list-style-type:none;padding:0}.placeholder-card ul li{padding:.7rem 0;border-bottom:1px solid hsla(0,0%,100%,.1);position:relative;padding-left:1.5rem}.placeholder-card ul li:before{content:"•";color:#fc3d21;position:absolute;left:0;top:.7rem}.placeholder-card ul li:last-child{border-bottom:none}