.app-footer{background-color:rgba(11,61,145,.2);backdrop-filter:blur(10px);border-top:1px solid hsla(0,0%,100%,.1);color:#fff;margin-top:3rem}.footer-content{max-width:1200px;margin:0 auto;padding:3rem 1rem 2rem;display:grid;grid-template-columns:1fr 1fr 1fr;gap:2rem}.footer-section h3{font-family:"Exo 2",sans-serif;color:gold;font-size:1.2rem;margin-top:0;margin-bottom:1rem;position:relative}.footer-section h3::after{content:"";position:absolute;bottom:-0.5rem;left:0;width:50px;height:2px;background-color:gold;border-radius:2px}.footer-section p{font-size:.9rem;line-height:1.6;opacity:.9}.social-links{display:flex;gap:1rem;margin-top:1.5rem}.social-links a{width:40px;height:40px;border-radius:50%;background-color:hsla(0,0%,100%,.1);display:flex;justify-content:center;align-items:center;color:#fff;transition:all .3s ease}.social-links a:hover{background-color:#0b3d91;color:gold;transform:translateY(-3px)}.social-links a svg{width:20px;height:20px}.footer-links{list-style:none;padding:0;margin:0}.footer-links li{margin-bottom:.5rem}.footer-links a{color:#e0e0e0;text-decoration:none;font-size:.9rem;transition:all .3s ease;display:inline-block;position:relative;padding-left:1rem}.footer-links a::before{content:"•";position:absolute;left:0;color:#fc3d21}.footer-links a:hover{color:gold;transform:translateX(3px)}.footer-bottom{max-width:1200px;margin:0 auto;padding:1.5rem 1rem;text-align:center;border-top:1px solid hsla(0,0%,100%,.1)}.footer-bottom p{margin:0;font-size:.9rem;opacity:.8}.footer-bottom .credits{margin-top:.5rem;font-size:.8rem;opacity:.6}@media(max-width: 576px){.footer-content{grid-template-columns:1fr;padding:2rem 1rem 1rem;gap:2rem}}@media(min-width: 577px)and (max-width: 768px){.footer-content{grid-template-columns:1fr 1fr}.footer-content .footer-section:first-child{grid-column:span 2}}